    id: 'velvety-cap',
    text: `At home, *350 I take the mushroom out of my pocket. *1500 It’s airy, *350 and the cap has a velvety texture. *1500 The glow is still there, *350 only brighter. *1500 For reasons I can’t explain, *350 I take a bite.`,
